import React, { useState, useEffect } from "react";
import { Layout, Carousel, Avatar, Tag, Row, Col } from "antd";
import { WrapperBanner, ItemWrapper, BannerContentPacks } from "./Styles";
import { DEFAULT_IMAGE, URL_PLACEHOLDER, URL_S3 } from "../../constants/";
import { isMobile } from "react-device-detect";
import { useSelector, useDispatch } from "react-redux";


const BannerPacks = ({ id, record, ...props }) => {
    const entity = useSelector(({ auth }) => {
        return auth.entity;
      });
    const [cover, setCover] = useState();
    const [loading, setLoading] = useState(true);
    useEffect(() => {
        if (!props.loading) {
            if (id && record) {
                let cover = record.gallery ? record.gallery.gallery_media && record.gallery.gallery_media[0] &&
                    record.gallery.gallery_media[0].path ?
                    `${URL_S3}/${record.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
                    : DEFAULT_IMAGE;
                setCover(cover);
            }
        }
        setLoading(props.loading);
    }, [props.loading])
    if (loading) return "Loading...";
    return (
        <WrapperBanner
            configuration={{
                primary_color: entity && entity.primary_color || "var(--color-primary);",
                secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
                text_color: entity && entity.text_color || "var(--color-white)"
            }}
            style={{
                background: `url(${cover})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                // backgroundPosition: "center!important"
            }}>
            <Carousel>
                <Row style={{
                    margin: "50px 10px"
                }} type="flex" justify="left">
                    <Col xl={16} lg={16} md={24} xs={24}>
                        <div className="style-name">
                            <span>{record.name_es}</span>
                        </div>
                        {record.city && <div className="styles-city">
                            <Avatar shape="square" size="small" src={"/images/Icon feather-map-pin.svg"} />
                            <span className="city-name">{record.city && record.city.name}</span>
                        </div>}
                    </Col>
                </Row>
            </Carousel>
        </WrapperBanner>
    );
};
export default BannerPacks;