import styled from 'styled-components';

export const Wrapper = styled.div`

/* stylos del search */
& .section-container-search{
    padding-top: 14px;
}
& .style-subtitle{
    display: flex;
    justify-content: space-between!important;
    font-family: 'Jost';
    font-weight: 600;
    font-size: 28px;
}
& .description-container h2{
    font-family: 'Jost';
    font-size: 49px;
    font-weight: 600;
} 
& .container-section-description{

/*     background: aqua; */
}
& .container-section-List{
/*     background: red; */
}

& .style-description{
        width: auto;
        font-family: 'Jost';
        font-weight: 500;
        font-size: 19px;
        padding-top: 24px;
        letter-spacing: 0px;
        color: #8A8A8A;
        opacity: 1;
    }
}
& .container-section-card{    
        /* background: brown; */
        width: auto;
        padding: 8px;
    }
& .ant-divider-horizontal {
  
    background: var(--color1)!important;
   
}

& .style-beneficio h2 {
    margin-top: 39px;
    margin-bottom: 20px;
    font-family: 'Jost';
    font-weight: 600;
    font-size: 27px;
}
& .style-Gallery h2 {
    margin-top: 39px;
    margin-bottom: 20px;
    font-family: 'Jost';
    font-weight: 600;
    font-size: 27px;
}
& .styles-title-b {
    font-family: 'Jost';
    font-size: 19px;
    font-weight: 500;
    color: #8A8A8A;
}
& .styles-dpoins {
    color: var(--color1)!important;
    font-family: 'Jost';
    font-size: 19px;
    font-weight: 500;
}
& .styles-dpoins span {
    font-family: 'Jost';
    font-weight: 600;
}
& .styles-dpoins span img {
    margin: 6px
}
@media (max-width:576px){
    .styled-row {
        margin: 0px -15px!important;
    }
    .container-section{
        padding-top: 24px!important;
    }
}
${({ configuration }) => {
    return `
        & .styles-dpoins >span{
            color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
        }
        & .style-name, .city-name {
            color:${configuration && configuration.text_color || "var(--color-gray-dark)"}!important;
        }
    
    `;
}}
`;
export const WrapperBanner = styled.div`
background-color:#333333;

`;
export const WrapperCardsPack = styled.div`
    & .ant-list {
        width: fit-content !important;
    }
    & .ant-list-item{
        display: flex !important;
        max-width: 100%;
        margin-bottom: 40px!important;
        padding-top: 10px!important;
        padding-bottom: 4px!important;
        border-bottom: none
    }
    & .ant-list-item-meta-content {
        
        display: block;
        padding-top: 20px;
        margin-top: 20px;
    }
    & .ant-list-item-meta-title {
        margin-bottom: -9px;
    }
`;