import React, { useEffect, useState } from "react";
import { Col, Layout, Row, Divider, Avatar, Button, Card, Input, message, Spin, Carousel } from "antd";
import BannerPacks from "../../components/banner/BannerPacks";
import { Router, Link } from "@reach/router";
import { Wrapper, WrapperCard, WrapperBanner } from "./Styles";
import MyList from "../../components/my-list/MyList";
import MyCard from "../../components/card/MyCard";
import { DEFAULT_IMAGE, URL_PLACEHOLDER, URL_S3 } from "../../constants/";
import { getService } from "../../services";
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";
import Loader from "../../components/loader/";
import MyGallery from "../../components/my-gallery/MyGallery";
import MapBox from "../../components/map/MapBox";
import { CHANGE_ENTITY } from "../../constants/actionTypes";
import SearchField from "../../components/search-field/SearchField";


const Packs = ({ t, reference = "packs", id, ...props }) => {
    const dispatch = useDispatch();
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity
    })
    const handleMapChange = (field, lngLat, form) => {
        let { lng, lat } = lngLat;
        console.log(field, lngLat, form)
        if (form) {
            form.setFieldsValue({
                lat,
                lng
            });
        }
    }
    const [record, setRecord] = useState({});
    const [loading, setLoading] = useState(true);
    const getData = () => {
        if (reference) {
            const service = getService(reference);
            service.get(id)
                .then(record => {

                    dispatch({
                        type: CHANGE_ENTITY,
                        entity: record.entity
                    })
                    setRecord(record)
                    setLoading(false);
                })
                .catch(err => {
                    setLoading(false);
                    message.error(err.message);
                });
        }
    }
    useEffect(() => {
        getData();
    }, [id]);

    if (loading) return <Loader />;
    return (
        <Wrapper configuration={{
            primary_color: entity && entity.primary_color || "var(--color-primary);",
            secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
            text_color: entity && entity.text_color || "var( --color-gray-dark)"
           }}>
            <BannerPacks id={id} record={record} loading={loading} />
            <Row className="section section-search" type="flex" justify="center" align="center" gutter={10}>
                <Col className="section-container-search" xl={10} lg={16} md={16} xs={22} >
                    <SearchField
                        placeholder={t("search.Text.Input")}
                        reference="packs"
                        open={false}
                        source={`name_${locale}`}
                    />
                </Col>
            </Row>
            <Row className="styled-row" style={{
                margin: "40px 10px"
            }} type="flex" justify="center">
                <Col span={20}>
                    <Row className="section section-container" justify="space-between" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
                        <Col className="container-section" xl={24} lg={24} md={24} xs={24}>
                            <div className="style-subtitle">
                                <h2>{t("packs.title.TextLabel")}</h2>
                            </div>
                        </Col>
                        <Col className="container-section-description" xl={16} lg={12} md={10} xs={24} >
                            <div className="style-description">
                                <div className="description-container" dangerouslySetInnerHTML={{ __html: record[`description_${locale}`] }} />
                            </div>
                            {<div className="my-list">
                                <MyList
                                    refinement={record => (record["item"])}
                                    loading={loading}
                                    reference="items-packs"
                                    filterDefaultValues={{
                                        pack_id: id
                                    }}
                                  />
                            </div>}
                        </Col>
                        <Col className="container-section-card" >
                            <div className="section-myCard">
                                <MyCard
                                    loading={loading}
                                    record={record} />
                            </div>
                        </Col>
                        {/* beneficios */}
                        <Col xl={16} lg={12} md={10} xs={24} >
                            <div className="style-beneficio">
                                <h2>{t("packs.title.Label")}</h2>
                            </div>
                            <div>
                                {/*  <div className="styles-title-b"><span>{t("packs.descriptions.Label")}</span></div> */}
                                <div className="styles-dpoins"><span><img src={"/images/Icon awesome-ticket-alt.svg"} />
                                </span>< span>{t("packs.descriptions.points")}</span>{""}<span>{(record.redeem_points) + "Pts"}</span></div>
                            </div>
                        </Col>

                        {/* gallery*/}
                        <Col className="section-galery" xl={24} lg={24} md={24} xs={24}>
                            <div className="style-Gallery">
                                <h2>{t("packs.text.LabelTitle")}</h2>
                            </div>
                            <MyGallery photos={record.gallery && record.gallery.gallery_media.map(media => ({
                                src: `${URL_S3}/${media.path}`,
                                width: 6,
                                height: 4
                            }))} />
                        </Col>
                        {/* mapa */}
                        <Col xl={24} className="section-map">
                            <div className="styles-map">
                                <MapBox
                                    data={[]}
                                    center={[
                                        -74.19904, 11.24079

                                    ]}
                                />
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Wrapper >
    );
};
export default translate()(Packs);