import React, { useEffect, useState } from "react";
import { Avatar, Button, Card, Spin, Tag, } from "antd";
import { Router, Link } from "@reach/router";
import { WrapperMyCard } from "./Styles";
import { DEFAULT_IMAGE, IMAGE_MAP_PIN, URL_PLACEHOLDER, URL_S3 } from "../../constants/";
import { money } from "../../utils/";
import { getService } from "../../services";
import * as actionTypes from "../../constants/actionTypes";
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";



const { Meta } = Card;
const MyCard = ({ t, record = {}, loading, id, ...props }) => {
  const dispatch = useDispatch();
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const show = useSelector(({ auth }) => {
    return auth.show;
  });
  const cart = useSelector(({ auth }) => {
    return auth.cart || [];
  });
  const user = useSelector(({ auth }) => {
    return auth.user;
  });
  const entity = useSelector(({ auth }) => {
    return auth.entity;
  });
  let isSelected = (typeof cart.find(it => (it.id == record.id)) != "undefined");
  const handleClick = () => {
    if (!isSelected) {
      if (!cart.find(it => (it.id == record.id)))
        dispatch({
          type: actionTypes.ADD_TO_CART,
          cart: [...cart, record]
        });
      if (cart.length == 0) {
        dispatch({
          type: actionTypes.SHOW_CAR,
          show: true
        });
      }
    }
    if (isSelected)
      dispatch({
        type: actionTypes.ADD_TO_CART,
        cart: cart.filter(it => (it.id != record.id))
      });
  }
  return (
    <WrapperMyCard configuration={{
      primary_color: entity && entity.primary_color || "var(--color-primary);",
      secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
      text_color: entity && entity.text_color || "var( --color-gray-dark)"
    }}>
      <Card
        actions={[
          <Button
            size="large"
            onClick={handleClick}
            className="btn-add-to-reservation"
            block
            type={!isSelected ? "primary" : "danger"}
          >
            {
              isSelected ? t("global.selectedText") :
                t("packs.addToReservation")
            }
          </Button>
        ]}
      >
        <Meta
          title={<div className="card-title-container">
            <div className="card-category">
              <Tag>{record.category_pack && record.category_pack.name}</Tag>
            </div>
            <div className="card-title">
              <h2>{record[`name_${locale}`]}</h2>
            </div>
            <div className="card-city">
              {record.city_id && <div className="avatar">
                <Avatar shape="square" size="small" src={IMAGE_MAP_PIN} />
                {<span>{record.city_id && record.city.name}</span>}
              </div>}
            </div>
          </div>}
          description={<div className="card-description-container">
            <div className="card-price-container">
              <div className="card-label">
                <span>{t("packs.labelPrice")}</span>
              </div>
              <div className="card-value">
                <span>{money(record.price_cop)}</span>
              </div>
            </div>
            {record.redeem_points && <div className="card-points-container">
              <div className="card-label">
                <span>{t("pack.card.titlePoint")}</span>
              </div>
              <div className="card-value">
                <span>{(record.redeem_points) + "Pts"}</span>
              </div>
            </div>}
          </div>}
        />
      </Card>

    </WrapperMyCard>
  );
};
export default translate()(MyCard);